
html,
body {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  margin: 0;
}
html *,
body *,
html *::before,
body *::before,
html *::after,
body *::after {
  box-sizing: inherit;
}
.app {
  font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  position: relative;
  text-align: center;
  margin: 0 auto;
}
.app__main {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 111px;
}
.app__main > div {
  position: absolute;
  left: 0;
  top: 0;
}
.app__button {
  position: fixed;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  height: 111px;
  bottom: 0;
  width: 100%;
}


